.App {
  text-align: center;
}

.flex-container {
    display: flex;
}

.flex-column {
    flex-direction: column;
}

.app-viewport {
    width: 100vw;
    height: 100vh;
}

.form-control.xs {
    padding: 5px;
    font-size: 1rem;
    line-height: 1;
    height: auto !important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
